@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";


@keyframes enter-left-card {
    0% {
        left: -50vw;
    }
    100% {
        left: 0vw;
    }
}

@keyframes enter-right-card {
    0% {
        right: -50vw;
    }
    100% {
        right: 0vw;
    }
}

.title-card {
    position: absolute;
    max-height: 30vh;
    width: 50%;
    bottom: 0;
    color: white;
    /*background-color: white;*/
    /*background-color:rgba(255, 255, 255, 0.877);*/
    /*border-radius: 0.5vw 0.5vw 0 0;*/
    overflow:hidden;
    display:flex;
    flex-direction: row;
    align-content:center;
    justify-content:space-between;
    align-items: flex-start;
    /*box-shadow: 0 -0.1vh 0.5vh black;*/
    z-index:98;
    flex-wrap:wrap;
    &--enter-left {
        animation: 1s ease-in forwards enter-left-card;
    }
    &--enter-right {
        animation: 1s ease-in forwards enter-right-card;
    }
    &__title {
        color: hsl(204, 100%, 41%);
        background-color: rgba(0, 0, 0, 0.692);
        font-size: 7vh;
        width: 100%;
        text-align:center;
        height: 9vh;
        width: 100%;
        font-family:"iceland";
        font-weight:bold;
        background-image: url("/connectwork.png");
        display:flex;
        flex-direction: row;
        align-items: center;
        justify-content:center;
        @include mobile-only {
            font-size: 5vw;
        }
        &:hover {
            color:orange;
        }
    }
    &__catchphrase {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: center;
        color: hsl(204, 100%, 11%);
        font-size: 2vw;
        width: 100%;
        text-align:center;
        height: 10vh;
        font-family: "Cabin sketch";
        background-image: url("/stripes-light.png");
        font-family:"iceland";
        @include mobile-only {
            font-size: 5vw;
            padding: 3vw;
        }
    }
}