@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.footer {
    width: 100vw;
    height: auto;
    z-index:99;
    color:white;
    font-family: "Gloria Hallelujah";
    display:flex;
    align-content: center;
    justify-content:space-evenly;
    flex-wrap: wrap;
    background-image: url("/connectwork.png");
    font-family:"iceland";
    &__information-container {
        width: 30vw;
        height: auto;
        font-size: 1.3vw;
        display: flex;
        flex-direction: column;

        justify-content:center;
        align-items: center;
        @include mobile-only {
            width:100vw;
            font-size: 4vw;
        }
        &--full-width {
            width: 100vw;
            justify-content: center;
            height: auto;
            flex-direction:row;
            border-top: 2px solid rgb(0, 151, 197);
            & img {
                width: 5vw;
                height:5vw;
            }
            & p {
                height: auto;
                width: 40vw;
                align-items: center;
                text-align: center;
            }
        }
    }
}