@keyframes enter-left-then-exit {
    0% {

        left: 0vw;
    }
    20% {
        left: 0vw;
        
    }
    80% {
        left: 0vw;
    }
    100% {
        left: 100vw;
    }
}

@keyframes enter-left {
    0% {

        left: -100vw;
    }
    100% {
        left: 0;
        
    }
}

.background-carousel {

    width: 100%;
    z-index: 1;
    flex-wrap:nowrap;
    background-color:transparent;

    &--full-width {
    width:100vw;
    min-height: 100vh;
    position: absolute;
    top: 0vh;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-x:hidden;
    }

    &__image {
        object-fit: cover;
        width: 100%;
        height: 100%;
        box-shadow:2px  2px 8px rgb(2, 2, 2);
        position:absolute;
        filter: saturate(1.2);
        &--current {
            left: -100vw;
            animation: 10s ease-in-out forwards enter-left-then-exit;
        }
        &--next{
            left: -100vw;
            animation: 2s ease-in-out 8s forwards enter-left;
        }
    }

    &__button {
        z-index:99;
        width: 20vw;
        height:10vh;
        display: none;
    }
}