@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.price-card {
    display: flex;
    flex-direction:column;
    align-items: center;
    flex-basis: 50%;
    /*background: rgba(0, 0, 0, 0.74);*/
    padding-bottom: 3vw;
    padding-top: 3vw;
    background-image: url("/connectwork.png");
    @include mobile-only {
        width: 100vw;
    }
    &__icon {
        font-size: 5vw;
        width: 8vw;
        height: 8vw;
        color: rgb(255, 0, 43);
        background-color:white;
        border: 1px solid red;
        border-radius: 50%;
        display:flex;
        flex-direction: row;
        justify-content:center;
        align-items:center;
        margin-bottom: 2vw;
        @include mobile-only {
            font-size: 15vw;
            width: 25vw;
            height: 25vw;
        }
    }
    &__product-name {
        font-family: "iceland";
        font-size: 2.5vw;
        color:white;
        @include mobile-only {
            font-size: 5vw;
        }
    }
    &__price {
        font-family: "iceland";
        font-size: 5vw;
        color:white;
        @include mobile-only {
            font-size: 6vw;

        }
        &::after {
            content: " ";
            font-family: 'Font Awesome 5 Free';
            font-weight:bold;
            font-size: 4vw;
        }
    }
}