@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.language-selector {
    
    &__icons {
        z-index:99;
        width:10vw;
        position: fixed;
        top: 3vh;
        right: 1vw;
        display:flex;
        flex-direction:row;
        justify-content: space-evenly;
        background:rgba(255, 255, 255, 0.637);
        border-radius: 2vw;
        border: 1px solid black;
        @include mobile-only {
            flex-direction: column;
            align-items: center;
            height: 8vh;
            min-width: 4vw;
            top: 1vh;
        }
        & img {
            object-fit:cover;
            width: 4vh;
            height: 4vh;
            @include mobile-only {
                flex-direction: column;
                width: 4vh;
                height: 4vh;
            }
        }
    }

}