@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.section-details {
    width:100%;
    height: auto;
    background-color: white;
    color:#55a7e9;
    display:flex;
    flex-direction:row;
    justify-content: space-between;
    align-content:center;
    border-top: 0.2vw solid rgb(255, 166, 0);
    flex-wrap: wrap;
    &__element {
        width: 33%;
        display:flex;
        flex-direction:column;
        align-items: center;

        padding: 1vw;
        padding-top: 2vw;
        box-sizing:border-box;
        @include mobile-only {
            width: 50%;
            padding-top: 4vw;
        }
        & i {
            font-size: 5vw;
            text-shadow: 0.1vw 0.1vw 0.1vw rgb(145, 145, 145);
            @include mobile-only {
                font-size: 15vw;
            }
        }
        & p {
            text-align: center;
            font-family: "iceland";
            font-size: 1.2vw;
            font-weight:bold;
            color:rgb(32, 2, 0);
            @include mobile-only {
                font-size: 5vw;
            }

        }
        
    }
}