html {
    height: -webkit-fill-available;
}

body {
    min-height: 100vh;
    min-height: -webkit-fill-available;
    display:flex;
    flex-direction:row;
    justify-content:space-around;
    align-content:center;
    overflow-x:hidden;
}

#root {
    display:flex;
    flex-direction:row;
    align-content:center;
    justify-content:space-around;
    flex-wrap:wrap;
    width: 100vw;
    min-height: 100vh;
    min-height: -webkit-fill-available;
    /*background: linear-gradient(0deg, rgb(231, 231, 231) 10%, rgba(255,255,255,1) 100%);*/
    /*background-image: url('../../src/background.jpg');*/
    overflow:hidden;

}

.card {
    display:flex;
    flex-direction:row;
    align-content:center;
    justify-content: space-around;
    align-items:center;
    margin-bottom:3vw;
    &--small {
        width:20vw;
        flex-direction:column;
    }
    &--reverse {
        flex-direction: row-reverse;
    }

}

.carousel {
    height: 100vh;
    width: 100vw;
}