@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.banner {
    display: flex;
    flex-direction:row;
    align-items:center;
    position: absolute;
    top:0;
    left: 0;
    right: 0;
    z-index:98;
    height: 10vh;
    background-color: rgba(10, 7, 0, 0.603);
    box-shadow: 0 -0.1vh 0.5vh white;
    
    
    margin-bottom: 0;

    &__title {
        color: rgb(255, 255, 255);
        font-size: 4vh;
        /*border-bottom: solid 3px black;*/
        width: 80vw;
        z-index: 99;
        font-family:"iceland";
        text-align:left;
        text-shadow: 0 0 1vh black;
        @include mobile-only {
            font-size: 5vw;
        }
    }

    &__logo {
        height:20vh;
        border-radius: 10vh;
        margin-bottom: -10vh;
        margin-right: 2vw;
        margin-left: 3vw;
        box-shadow:2px  2px 8px rgb(2, 2, 2);
        z-index: 99;
        @include mobile-only {
            height: 15vw;
            margin-bottom: 0;
        }
    }
}