@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.photo-card {
    width: 33%;
    height: 100%;
    display:flex;
    flex-direction:column;
    flex-wrap: no-wrap;
    /*margin: 5vh 0;*/
    justify-content:space-around;
    /*background-color: white;*/
    align-items:center;

    @include mobile-only {
        width:100%;
    }
    &--reverse {
        flex-direction: row-reverse;
    }
    &__image {

        height: 20vw;
        width: 33vw;
        object-fit: cover;
        /*border-radius: 0.5vw;
        box-shadow: 0.3vw.. 0.3vw 0.3vw black;*/
        border-bottom: 0.2vw solid rgb(255, 166, 0);
        border-top: 0.2vw solid rgb(255, 166, 0);

        @include mobile-only {
            height: 66vw;
            width: 100vw;
        }
    }   
    &__text {
        width: 80%;
        height:max-content;
        text-align: justify;
        font-size:1.3vw;
        height: auto;
        font-family: "iceland";
        padding: 2vh;
        box-sizing:border-box;
        @include mobile-only {
            font-size: 5vw;
        }


    }
}