@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.who-we-are {
    width: 100%;
    height: auto;
    display:flex;
    flex-direction:column;
    flex-wrap: nowrap;
    /*margin: 5vh 0;*/
    justify-content:space-around;
    /*background-color: white;*/
    align-items:center;
    background-image: url("/what-the-hex.png");
    

    @include mobile-only {
        width:100%;
    }

    &__title {
        font-size: 3vw;
        font-family: "Gloria Hallelujah";
        width:100%;
        text-align:center;
        color:white;
        background-image: url("/alu.png");
        background-repeat: repeat;
        @include mobile-only {
            font-size: 6vw;
        }
    }   
    &--reverse {
        flex-direction: row-reverse;
    }
    &__images-container {
        width: 100%;
        display:flex;
        flex-direction:row;
        justify-content: space-evenly;
    }
    &__image {
        height: 20vw;
        width: 20vw;
        object-fit: cover;
        border-radius: 50%;
        box-shadow: 0.3vw 0.3vw 0.3vw black;
        border-bottom: 0.2vw solid rgb(255, 166, 0);
        border-top: 0.2vw solid rgb(255, 166, 0);
        margin-top: 1vw;
        z-index: 99;

        @include mobile-only {
            height: 60vw;
            width: 60vw;
            @include mobile-only {
                height:40vw;
                width: 40vw;
            }
        }

        &:nth-child(odd) {
            width: 15vw;
            height: 15vw;
            margin-top: -6vw;
            @include mobile-only {
                height: 30vw;
                width: 30vw;
            }
        }
    }   
    &__text {
        width: 100vw;
        background-color: rgba(255, 255, 255, 0.52);
        padding: 10vw 20vw 2vw 20vw;
        margin-top: -10vw;
        height:max-content;
        text-align: center;
        font-size:2vw;
        height: auto;
        font-family: "iceland";
        box-sizing:border-box;
        border: 3px solid rgb(0, 141, 197);
        /*box-shadow:0.2vw 0.2vw 0.1vw rgba(0, 0, 0, 0.52);*/
        @include mobile-only {
            font-size: 5vw;
        }


    }
}