@import "/src/styles/variables.scss";
@import "/src/styles/mixins.scss";

.generic-main-content {
    height: auto;
    background-color: transparent;
    z-index:8;
    position:relative;
    display: flex;
    flex-direction:row;
    justify-content:center;
    flex-wrap:wrap;
    background-color:white;
    background-image: url("/dot-grid.png");

    @include mobile-only {
        flex-direction: column;
    }

    /*box-shadow: 0.3vw 0.3vw 0.3vw black;*/
    &__title {
        font-size: 3vw;
        font-family: "Gloria Hallelujah";
        width:100%;
        text-align:center;
        color:white;
        background-image: url("/alu.png");
        background-repeat: repeat;
        @include mobile-only {
            font-size: 6vw;
        }
        

        &--pricing {
            /*background: rgba(255, 136, 0, 0.74);*/
            background-image: url("/stripes-light.png");
            color:orange;
            /*clip-path: polygon(10% 0, 90% 0, 100% 100%, 0 100%);*/
            
        }
    }
}